@import url(https://fonts.googleapis.com/css?family=Blinker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

p {
  margin: 0;
}

.containerApp {
  text-align: center;
  color: white;
  font-family: "Blinker", sans-serif;
  overflow: hidden;
  border: 5px solid black;
  border-radius: 100px 100px 10px 10px;
  width: 60%;
  margin: 150px auto 0 auto;
  background: #41436e;
}

.containerSB {
  margin-top: 15px;
}

.containerSession, .containerBreak {
  padding-bottom: 5px;
}

.containerSession p, .containerBreak p {
  margin: 10px auto;
}

.containerSession button, .containerBreak button {
  font-style: bold;
  width: 25px;
  height: 25px;
  color: white;
  background: none;
  border: 2px solid white;
  border-radius: 50%;
  margin-bottom: 5px;
}

.textSB {
  font-size: 1.5em;
}

.containerClock {
  border-top: 3px solid black;
  height: 200px;
}

.containerClock button {
  font-style: bold;
  height: 30px;
  width: 100px;
  color: white;
  background: none;
  border: 2px solid white;
  border-radius: 50%;
  margin-bottom: 5px;
}

.time {
  color: black;
  border: 1px solid black;
  width: 50%;
  border-radius: 5px;
  margin: 20px auto 0 auto;
  background: #fc7b03;
  font-size: 2em;
  font-style: bold;
}

@media screen and (min-width: 600px) {
  .containerSession, .containerBreak {
    display: inline-block;
    width: 48%;
  }
  .time {
    font-size: 4em;
  }
  .containerSB {
    margin-top: 0px;
  }
}
